const Letter = () => {
    return (
        <h2>
            Dearest,
            
            I remember the special day when I turned the quarter. We were at the finest Indonesian restaurant that I could found in Berlin. You ate Sate for the first time and we discussed about the interior of the restaurant. It was very clear in my memory, how the way you stared me, smiled to me, and hold my hand has nothing changed until now…
            
            Back then, we had been together only for 3 weeks, very excited, curious, and lovely. However, there was something I regret from that time, I was not able to live fully at the moment with you, uncertainty was always let me down sometimes. But it was you, your warm-hearted, that made me want to say to my young me “It is a life, there’re always ups and downs, and you are gonna work on it together with a man in front of you”. Gladly we have made this far :)
            
            I really thanked for everything you did to me at that time, your lovely presents and the time together. It was the best birthday dinner I ever had. Followed by many birthday dinners together afterwards. I feel I am a luckiest girl in the world by having you.
            
            Berlin, 11th August 2043
            
            Yours
            
</h2>
    )
}


export default Letter


